import React, { Component } from "react";
import background from "../background.png";
import Navigation from "./navigation";
import firebasedb from "../firebase";
import Loading from './loading_screen';


class Results extends Component {
  state = {
    DBdata: [],
  };

  componentDidMount() {
    let found = false;
    let data = {};
    let DBdata = [];
    firebasedb.child("Mot").on("value", (snapshot) => {
      if (snapshot.val() != null) {
        data = { ...snapshot.val() };
        Object.keys(data).map((id) => {
          if (
            data[id].mot.toUpperCase() ===
            this.props.match.params.name.toUpperCase()
          ) {
            found = true;
            DBdata.push(data[id]);
            this.setState({ DBdata });
          }
        });
      } else {
      }
      if (!found) console.log("not found");
    });
  }

  render() {
    if (this.state.DBdata.length === 0) {
      return <Loading />;
    }

    return (
      <section className="result_page">
        <Navigation />
        <img className="backimg backimg2" src={background} alt="" />
        <div className="data">
          <div className="data_heading center">{this.state.DBdata[0].mot}</div>
          <div className="data_element">
            <h3 className="element_title">L'origine Du Mot</h3>
            <h2 className="element_details">{this.state.DBdata[0].origine}</h2>
          </div>
          <div className="data_element">
            <h3 className="element_title">Wilaya D'origine</h3>
            <h2 className="element_details">{this.state.DBdata[0].wilaya}</h2>
          </div>
          <div className="data_element">
            <h3 className="element_title">Synonymes</h3>
            <h2 className="element_details">
              {this.state.DBdata[0].Synonymes}
            </h2>
          </div>
          <div className="data_element">
            <h3 className="element_title">Definition</h3>
            <h2 className="element_details">
              {this.state.DBdata[0].definition}
            </h2>
          </div>
        </div>
      </section>
    );
  }
}

export default Results;
